// App.js
import React, {Component} from 'react';
import { ThemeProvider } from 'styled-components';
import { useDarkMode } from './useDarkMode';
import { lightTheme, darkTheme } from './theme';
import { GlobalStyles } from './global';
import Toggle from './Toggle';
import { Button, Container, Row, Col } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import MarcosText from './MarcosText';
import Media from 'react-bootstrap/Media';
import logo from './icons/pp.jpg';


function App() {
  const [theme, toggleTheme, componentMounted] = useDarkMode();
  const themeMode = theme === 'light' ? lightTheme : darkTheme;

  if (!componentMounted) {
    return <div />
  };


  return (
    <ThemeProvider theme={themeMode}>
      <>
        <GlobalStyles />

        <Container fluid>
          <Row className="justify-content-md-center" >
            <Col sm={9}> <h1>Marco Barbato</h1></Col>
            <Col sm={3}> <Toggle theme={theme} toggleTheme={toggleTheme} /></Col>
          </Row>
        </Container>
        <hr></hr>
        <MarcosText></MarcosText>

        <footer>
  <Container>
  <Media>
  <Media.Body>
    <Col sm={9}><h2>About Me</h2>
    <p>
      I'm a freshly graduated student with a passion for learning new stuff.
      This is my first project using react, so feel free to send me feedback.
    </p></Col>
  </Media.Body>
  <img src={logo} rounded  alt="logo"width="300" height="300" alt="Moon free icon" title="Moon free icon"  />
</Media>
</Container>

        </footer>
      </>
    </ThemeProvider>
  );
}

export default App;

